<template>
    <div class="FillMessage">
      <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>

      <div class="fiexoverTitle mt24 border_E3E4E8"  @click="SelectShow = true,cut=true">
        <div class="fiexover">
          <p>
            开票类型
          </p>
          <div class="cl_333">
            {{SelectText}}
            <i class="iconfont"></i>
          </div>
        </div>
      </div>
      <div class="fiexoverTitle border_E3E4E8 "  @click="showPicker = true">
        <div class="fiexover">
          <p>
            开票内容
          </p>
          <div class="cl_333">
            {{Pickercontent}}
            <i class="iconfont"></i>
          </div>
        </div>
      </div>
      <div class="fiexoverTitle border_E3E4E8">
        <div class="fiexover">
          <p>总金额</p>
          <div style="height: 100%;line-height: 1.02rem" class="f34 cl_333">
           <span class="cl_f90 f34">￥{{AggregateAmount}}</span>
          </div>
        </div>
      </div>

<!--      选择开票信息-->
      <div class="fiexoverTitle mt24 border_E3E4E8"  @click="SelectShow = true,cut=false">
        <div class="fiexover">
          <p>
            选择开票信息
          </p>
          <div class="cl_333">
            {{message}}
            <i class="iconfont"></i>
          </div>
        </div>
      </div>
<!--      发票抬头-->
      <div class="fiexoverTitle">
        <div class="fiexover border_E3E4E8">
          <p><i class="asterisk">*</i> 发票抬头</p>
          <div class="inputperaut">
            <input type="text" placeholder="请输入" @input="TimeMonitor()" v-model="input1">

          </div>
        </div>
      </div>
<!--      税号/身份证-->
      <div class="fiexoverTitle">
        <div class="fiexover border_E3E4E8">
          <p><i class="asterisk">*</i> 税号/身份证</p>
          <div class="inputperaut">
            <input type="text"  placeholder="请输入" @input="TimeMonitor()" v-model="input2">

          </div>
        </div>
      </div>
<!--      开户行-->
      <div class="fiexoverTitle border_E3E4E8">
        <div class="fiexover ">
          <p> 开户行</p>
          <div class="inputperaut">
            <input type="text"  placeholder="请输入" @input="TimeMonitor()" v-model="input3">

          </div>
        </div>
      </div>
<!--      银行账号-->
      <div class="fiexoverTitle border_E3E4E8">
        <div class="fiexover ">
          <p> 银行账号</p>
          <div class="inputperaut">
            <input type="text"placeholder="请输入" @input="TimeMonitor()" v-model="input4">

          </div>
        </div>
      </div>
<!--      地址-->
      <div class="fiexoverTitle border_E3E4E8">
        <div class="fiexover ">
          <p> 地址</p>
          <div class="inputperaut">
            <input type="text" placeholder="请输入" @input="TimeMonitor()" v-model="input5">

          </div>
        </div>
      </div>
<!--      电话-->
      <div class="fiexoverTitle border_E3E4E8">
        <div class="fiexover ">
          <p> 电话</p>
          <div  class="inputperaut">
            <input type="text"  placeholder="请输入" @input="TimeMonitor()" v-model="input6">

          </div>
        </div>
      </div>
<!--      接收邮箱-->
      <div class="fiexoverTitle border_E3E4E8">
        <div class="fiexover">
          <p> 接收邮箱</p>
          <div class="inputperaut">
            <input type="text"  placeholder="请输入" @input="TimeMonitor()" v-model="input7">
          </div>
        </div>
      </div>
      <div class="public-btn mt60  mr30 ml30" :class="SubmitClassbox==true?'public-btn':'public-btnNo'" @click="SubmitClassClick" @input="TimeMonitor()">
        提交
      </div>
      <router-link  :to="{path:'DrawerMessage'}" class="cancel">取消</router-link>
<!--      第一个-->
      <van-action-sheet v-model="SelectShow" :actions="cut==true?actions:action" @select="onSelect" :round=false />
<!--      第二个-->
      <van-popup v-model="showPicker" round position="bottom">
        <van-picker
          show-toolbar
          :columns="columns"
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>

    </div>

</template>

<script>
    import Vue from "vue"
    import HomePageTop from "../../common/HomePageTop";
    export default {
        name: "FillMessage",
        components: {HomePageTop},
        data() {
            return {
              //切换开票类型和选择开票信息的弹窗内容
              cut:true,
              //像子组件传值
              TopTotle: {name: "填写发票信息",left: true},
              //第一个的弹窗
              SelectText:"",
              //第一个的弹窗
              SelectShow:false,
              //第一个的弹窗数据
              actions:[{name: '电子普通发票'}, { name: '别的发票类型'}, ],
              action:[{name: '捷配(普通)'}, { name: '捷配(高端)'}, ],
              message:"",
              //第二个的弹窗
              columns: ['线路板', 'PCB', 'PCBA', '电子线路板', '元器件',],
              showPicker:false,
              Pickercontent:"",
              //前面传递的总金额
              AggregateAmount:"454.75",
              //切换按钮的颜色
              SubmitClassbox:false,
              //各个输入框
              input1:"",
              input2:"",
              input3:"",
              input4:"",
              input5:"",
              input6:"",
              input7:"",

            }
        },
        methods: {
          //提交
          SubmitClassClick(){
            if(this.SubmitClassbox==true){
              this.$router.push({
                path:"Succeed"
              })
            }
          },
          //第一个弹窗数据
          onSelect(item) {
            if(this.cut==true){
              this.SelectText = item.name;
            }else{
              this.message = item.name;
            }
            this.SelectShow = false;
            this.TimeMonitor();

          },
          //第二个弹窗方法

          onChange(picker, value, index) {
            console.log(`当前值：${value}, 当前索引：${index}`);
          },
          onCancel() {
            console.log('取消');
          },
          onConfirm(value) {
            // this.value = value;
            this.Pickercontent=value;
            this.showPicker = false;
            this.TimeMonitor();
          },
          //时刻监听
          TimeMonitor(){
            if(this.SelectText!=""&&this.Pickercontent!=""&&this.message!=""&&this.input1!=""&&this.input2!=""&&this.input3!=""&&this.input4!=""&&this.input5!=""&&this.input6!=""&&this.input7!=""){
                this.SubmitClassbox=true;

            }else{
              this.SubmitClassbox=false
            }





          },
        },
        created() {

        }
    }

</script>

<style >
  body,html {
    background: #F4F6F9 !important;
  }
  .asterisk{
    color: red;vertical-align: middle;display: inline-block;
  }
  .cancel{
    width:6.9rem;height: .96rem;background:#fff;border: 1px solid #f90;text-align: center;color: #f90;line-height: .96rem;font-size: .34rem;margin: .32rem auto;display: block
  }
  .inputperaut{height: 100%;line-height: 1.02rem;font-size: .34rem;color: #333;

  }
  .fiexoverTitle input{ border: none;text-align: right}
  .HomePageTop .Head {
    background: #fff;
  }
</style>



